@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
::-webkit-scrollbar {
  display: none;
}

@-moz-document url-prefix() {
  html,body{overflow: hidden !important;}
}

body, * {
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
}

*:focus {
  outline: none;
}

body {
  background: #100F0F;
  color: #D9D9D9;
  scroll-behavior: smooth;
}

@media (max-width: 1024px) {
  ::-webkit-scrollbar {
    display: inline;
    display: initial;
  }
  
  @-moz-document url-prefix() {
    html,body{overflow: scroll}
  }
}
.App {
  text-align: center;
  position: relative;
  z-index: 0;
}

.particlebg {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.home-left {
    width: 45%;
    height: 100%;
    background: #1F005D;
    text-align: left;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    padding-left: 5%;
    line-height: 1;
    position: relative;
    z-index: 0;
    overflow: hidden;
}

.home-left-contents {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.home-left .home-left-contents h1, 
.home-left .home-left-contents h2 {
    width: 100%;
    margin: 15px auto;
}

.home-left .home-left-contents h2 {
    margin-top: 0;
}

.home-left .home-left-contents h1 {
    font-size: 4em;
}

.home-left .home-left-contents span {
    font-size: 20px;
}

.home-left .home-left-contents hr {
    margin-right: 10px;
    height: 3px;
    width: 60px;
    background: #910bc5;
    border: 0;
    display: inline-block;   
}

.cv {
    display: block;
    text-decoration: none;
}

.button {
    background: transparent;
    color: #D9D9D9;
    border: 1px solid #D9D9D9;
    border-radius: 5px;
    padding: 15px;
    font-size: 1em;
    cursor: pointer;
    position: relative;
    margin-top: 20px;
    overflow: hidden;
    z-index: 9;
}

.button::after {
    content: "";
    width: 0;
    height: 100%;
    background: #D9D9D9;
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.5s all;
    z-index: -1;
}

.button:hover {
    color: #1F005D;
}

.button:hover.button::after {
    width: 100%;
}

@media (max-width: 768px) {
    .home-left {
        width: 100%;
        height: 40%;
    }
    .home-left .home-left-contents h1 {
        margin-top: 20px;
        font-size: 3em;
    }
    .home-left .home-left-contents h2 span span.what-am-i {
        font-size: 1em;
    }
    .home-left .home-left-contents hr {
        width: 50px;
    }
    .home-left .home-left-contents .button {
        padding: 10px;
        font-size: 0.8em;
    }
}

@media(max-width: 1024px) and (max-height: 1366px){
    .home-left {
        width: 100%;
        height: 40%;
    }
}

@media (max-width: 1440px) and (min-width: 768px){
    .home-left .home-left-contents h1 {
        font-size: 3em;
    }
}

@media (max-width: 1024px) and (max-height: 768px) {
    .home-left {
        width: 45%;
        height: 100%;
    }
}

@media (max-width: 767px) and (max-height: 764px){
    .home-left {
