.nav {
    position: fixed;
    z-index: 9;
    left: 10px;
    top: 45px;
    display: flex;
    transition: 0.5s all;
}

.nav-lists {
    line-height: 3;
    background: #100F0F;
    padding: 35px 40px;
    border: 1px solid #D9D9D9;
    border-radius: 10px;
    height: 203.636px;
}

.nav-lists p {
    color: #D9D9D9;
}

.nav-lists p:hover {
    border-radius: 10px;
    cursor: pointer;
    border-bottom: 1px solid #910bc5;
}

.expander {
    padding: 20px 5px;
    border: 1px double #D9D9D9;
    border-radius: 10px;
    background: #100F0F;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-left: 0;
    height: 30px;
    position: relative;
    z-index: 0;
}

.about-left {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 1s;
}

.about-left-contents {
    text-align: left;
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.about-left-contents hr, .about-left-contents>span, .about-left-contents h2, .about-left-contents p {
    margin: 10px 0;
}

.about-left-contents h2,
.about-left-contents p {
    width: 100%;
}

.about-left-contents h2 {
    font-size: 2em;
}

.about-left-contents p {
    line-height: 1.5;
}

.about-left-contents hr {
    margin-right: 10px;
    height: 3px;
    width: 60px;
    background: #910bc5;
    border: 0;
    display: inline-block; 
}

.social-media {
    margin: 10px 0;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.social-media a {
    margin: 5px;
}

.social-media a:first-child {
