.skill {
    display: flex;
    margin: 30px auto;
    width: 100%;
    align-items: center;
}

.skill *{
    margin: 0 5px;
}

.skill img:nth-of-type(odd){
    margin-left: 0;
}

.skill span{
    font-weight: bold;
}

.skill img {
    width: 32px;
    height: 32px;
}

.skill .React {
    -webkit-animation: spinning 5s infinite linear;
            animation: spinning 5s infinite linear;
}

.skill .skill-div {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
} 

.skill .skill-div .skill-num {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.skill .skill-div .skill-num span {
    margin-left: 0;
}

.skill .skill-div .skill-meter{
    margin-top: 5px;
    height: 10px;
    background: #D9D9D9;
    border-radius: 10px;
    transition: 1s;
}

@-webkit-keyframes spinning {
    from {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    } to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spinning {
    from {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    } to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
