.card {
    width:80%;
    height: 400px;
    margin: 0 50px;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    margin: 0 auto;
}

.card .project-images {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    transition: 0.5s all;
}

.card .card-text{
    height: 7%;
    width: 89%;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    text-align: left;
    color: #D9D9D9;
    background: rgba(0, 0, 0, 0.8);
    padding: 20px;
}

.card .card-text h2, .card .card-text p, .card .card-text a {
    margin: 16px 0;
}

.card .card-text h2{
    font-weight: bold;
    margin-top: 0;
}

.card .card-text a {
    color: #D9D9D9;
    margin-bottom: 0;
    text-decoration: none;
    display: block;
}

.card .card-text a:hover {
    text-decoration: underline;
}

.card-text:hover{
    height: auto;
}

@media (max-width: 450px) {
    .card {
        width: 90%;
    }
}
.projects {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.projects-content {
