    height: 80%;
}

.projects-header {
    width: 100%;
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.carousel {
    width: 100%;
    height: 80%;
    border-radius: 10px;
}

.react-multiple-carousel__arrow--left {
    left: 15px;
}

.react-multiple-carousel__arrow--right {
